/* Обнуление стилей */

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

a:focus, a:active {
  outline: none
}

nav, footer, header, aside {
  display: block
}

html, body {
  width: 100%;
  height: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit
}

input:focus, textarea:focus {
  outline: none;
}

input::-ms-clear {
  display: none
}

input::placeholder {
  color: inherit
}

button {
  cursor: pointer;
  background-color: transparent;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1 {
  /* font-family: 'Bebas Neue', sans-serif; */
  /* font-weight: 700; */
}



.App {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  
}
